us {\r\n  top: 30px;\r\n}\r\n\r\nheader {\r\n  top: 0;\r\n  position: sticky;\r\n  z-index: 100;\r\n}\r\n\r\n/* nav */\r\nnav {\r\n  position: relative;\r\n  width: 100%;\r\n  background-color: white;\r\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\r\n}\r\n\r\n.nav-container {\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: space-between;\r\n  height: 100%;\r\n}\r\n\r\n.brand a {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  font-size: 20px;\r\n  font-weight: bold;\r\n  text-decoration: none;\r\n  color: var(--secondary-color);\r\n}\r\n\r\n.nav__menu {\r\n  width: max-content;\r\n  height: 100%;\r\n}\r\n\r\n.nav__menu ul {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  gap: 60px;\r\n  list-style: none;\r\n}\r\n\r\n.nav__menu ul li {\r\n  padding: 0;\r\n}\r\n\r\n.nav__menu a {\r\n  text-decoration: none;\r\n  font-size: 16px;\r\n  padding: 0.9rem;\r\n}\r\n\r\n.nav__menu ul li:hover {\r\n  color: var(--secondary-color);\r\n  text-decoration: underline;\r\n}\r\n\r\n\r\n/* hero */\r\n\r\n.hero-container {\r\n  min-height: 380px;\r\n  position: relative;\r\n  background-image: url('../public/images/sharp/hero-image_4-large.jpg');\r\n  width: 100%;\r\n  background-position: center;\r\n  background-repeat: no-repeat;\r\n  background-size: cover;\r\n  opacity: 0.9;\r\n}\r\n\r\n.hero-container .hero-content-container {\r\n  position: absolute;\r\n  top: 50%;\r\n  left: 50%;\r\n  transform: translate(-50%, -50%);\r\n  text-align: center;\r\n}\r\n\r\n.hero-container .hero-content-container > h1,\r\np {\r\n  font-weight: bold;\r\n  line-height: 2em;\r\n}\r\n.hero-text {\r\n  color: var(--third-color);\r\n}\r\n\r\n/* Content */\r\nsection {\r\n  margin-top: 40px;\r\n}\r\n\r\na,\r\np,\r\nh1,\r\nh2,\r\nh3,\r\nh4 {\r\n  text-decoration: none;\r\n}\r\n\r\n.main-resto_container {\r\n  margin: 60px auto;\r\n}\r\n\r\n.main-resto_label {\r\n  margin: auto;\r\n  width: max-content;\r\n  font-size: 22px;\r\n}\r\n\r\n.main-resto_label hr {\r\n  margin-top: 0;\r\n  width: 50%;\r\n  border-bottom: 1px solid var(--secondary-color);\r\n}\r\n\r\n.list-resto {\r\n  margin-top: 50px;\r\n  text-align: left;\r\n  display: grid;\r\n  grid-gap: 16px;\r\n}\r\n\r\n.resto-item {\r\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\r\n  width: 100%;\r\n  border-radius: 15px;\r\n  overflow: hidden;\r\n  position: relative;\r\n  top: 0;\r\n  transition: top ease 0.4s;\r\n  cursor: pointer;\r\n}\r\n\r\n.resto-item:hover {\r\n  top: -4px;\r\n}\r\n\r\n.resto-title {\r\n  display: flex;\r\n  justify-content: space-between;\r\n}\r\n\r\n.resto-item_image {\r\n  object-fit: cover;\r\n  width: 100%;\r\n  height: 250px;\r\n  margin-bottom: 15px;\r\n}\r\n\r\n.resto-item_city {\r\n  font-size: 16px;\r\n  text-transform: uppercase;\r\n  color: var(--secondary-color);\r\n  padding: 0 16px 0 16px;\r\n}\r\n\r\n.resto-item_rating {\r\n  color: var(--secondary-color);\r\n  font-weight: 600;\r\n  text-decoration: none;\r\n  margin: 0 0 0 5px;\r\n  padding: 2px 3px 2px 3px;\r\n  border-radius: 5px;\r\n}\r\n\r\n.resto-item_name {\r\n  font-weight: bold;\r\n  font-size: 24px;\r\n  margin-top: 16px;\r\n  transition: 0.3s opacity;\r\n  padding: 0 16px 0 16px;\r\n}\r\n\r\n.resto-item_name:hover {\r\n  opacity: 0.7;\r\n}\r\n\r\n.resto-item_name {\r\n  text-decoration: none;\r\n  color: var(--secondary-color);\r\n  font-size: 24px;\r\n}\r\n\r\n.resto-item_desc {\r\n  display: -webkit-box;\r\n  -webkit-box-orient: vertical;\r\n  -webkit-line-clamp: 5;\r\n  font-weight: 500;\r\n  text-overflow: ellipsis;\r\n  overflow: hidden;\r\n  margin: 16px 15px;\r\n  font-size: 14px;\r\n  line-height: 1.5em;\r\n  color: black;\r\n}\r\n\r\n/* Footer */\r\nfooter {\r\n  text-align: center;\r\n  width: 100%;\r\n  background-color: var(--secondary-color);\r\n  padding: 30px 0;\r\n  line-height: 3em;\r\n}\r\n.footer-text {\r\n  color: var(--third-color);\r\n}\r\n\r\n#root {\r\n  padding-top: 65px;\r\n}\r\n\r\n.restaurant {\r\n